___CSS_LOADER_EXPORT___.push([module.id, "@media only screen and (max-width:767.98px){.vhd-container:not(.desktop) .vhd-picker{padding:8px;width:100%!important}.tour-module .cont .row{width:100%!important}.qty input{border:0;width:50%}}@media screen and (max-width:477px){.vhd-input{min-width:100%!important}.vhd-container{width:100%!important}}@media screen and (max-width:576px){.tour-module .cont{height:100%!important}}.vhd-picker{min-height:362px!important;padding:18px!important;z-index:2!important}.vhd-input{border:1px solid orange!important;border-radius:20px;color:#062348!important;font-family:Open Sans!important;font-size:16px;font-weight:900;height:40px!important;line-height:32px;min-height:40px;outline:none;padding:8px;width:100%}.vhd-input::-moz-placeholder{color:#062348!important}.vhd-input::placeholder{color:#062348!important}.vhd-container:not(.desktop) .vhd-picker{min-height:362px}.vhd-calendar .calendar-date .week .day[data-v-0cb4ff92]{color:#505050;cursor:pointer;font-size:16px;font-weight:500;line-height:30px;text-align:center}.vhd-calendar .calendar-date .week .day{background-image:none!important;height:30px!important}.vhd-calendar-header{height:20px!important}.vhd-container:not(.desktop) .vhd-calendar-header{height:60px!important}.vhd-container:not(.desktop) .vhd-calendar-header>.info{display:block;height:60px;padding-top:32px!important;width:100%}.vhd-container:not(.desktop) .vhd-calendar-footer{height:36px!important}.vhd-calendar-footer{height:24px!important}.vhd-calendar .calendar-date .week{height:30px!important}.vhd-calendar .calendar-date .week .day.end-date:after,.vhd-calendar .calendar-date .week .day.start-date:before{background-color:orange!important}.vhd-calendar .calendar-date .week .day.end-date,.vhd-calendar .calendar-date .week .day.in-date-range,.vhd-calendar .calendar-date .week .day.start-date{background-color:#ffa50070!important}.vhd-calendar .calendar-date .week .day.today{border:1px solid orange!important}.vhd-calendar-footer .confirm,.vhd-calendar-footer .reset{font-family:Open Sans!important;font-weight:900!important}.vhd-calendar-footer .reset{color:#062348!important}.vhd-calendar-footer .confirm{color:orange!important}.vhd-calendar .calendar-month-title,.vhd-calendar .calendar-week-item{color:#062348!important;font-family:Open Sans!important;font-weight:900!important}.values__hotel-rating:after{background-image:url(data:image/png;base64,);background-position:50%;background-repeat:no-repeat;content:\"\";display:inline-block;height:12px;margin-left:2px;width:13px}.multiselect__tags .price{color:#062348;font-family:Open Sans!important;font-weight:900!important}.multiselect__tags .price:focus{box-shadow:none!important;outline:none!important}.multiselect-title{color:#062348!important;font-family:Bello Pro}.multiselect__tags{border:1px solid #f08b23;border-radius:20px;font-size:1rem;min-height:40px}.multiselect__tag{background:#062348}.multiselect__tag-icon:after{color:#fff}.multiselect__option--highlight,.multiselect__option--highlight:after,.multiselect__tag-icon:focus,.multiselect__tag-icon:hover{background:#f08b23}.multiselect{font-family:Open Sans!important;font-weight:900}.multiselect,.multiselect__placeholder{color:#062348}.multiselect__input:focus,.multiselect__single:focus{border-color:#062348}.multiselect__input::-moz-placeholder{color:#062348!important;font-family:Open Sans!important;font-weight:700}.multiselect__input::placeholder{color:#062348!important;font-family:Open Sans!important;font-weight:700}.multiselect--disabled{background:#fff}.multiselect--disabled .multiselect__select{background:transparent}.multiselect__spinner:after,.multiselect__spinner:before{background:transparent;border:3px solid transparent;border-radius:100%;border-top-color:orange;box-shadow:0 0 0 1px transparent;content:\"\";height:16px;left:50%;margin:-8px 0 0 -8px;position:absolute;top:50%;width:16px}.multiselect__spinner{border-radius:50px}.qty .count{color:#0f213d!important;font-family:Open Sans;font-size:25px;font-weight:700;line-height:30px;min-width:35px;padding:0 2px}.qty .count,.qty button{display:inline-block;text-align:center;vertical-align:top}.qty button{background:#f08b23;border-radius:50%;color:#fff;cursor:pointer;font:30px/1 Arial,sans-serif;height:30px;padding:0;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;width:30px}.qty div{text-align:center}.qty .minus:hover,.qty .plus:hover{background:#0f213d!important;color:#fff}.qty input{border:0;width:60%}.qty input::-webkit-inner-spin-button,.qty input::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}.qty input:disabled{background-color:#fff}", "",{"version":3,"sources":["webpack://./resources/js/desktop/pages/Manager/Tours/Create.vue"],"names":[],"mappings":"AAuvCA,4CACA,yCAEA,WAAA,CADA,oBAEA,CACA,wBACA,oBACA,CACA,WACA,QAAA,CACA,SACA,CACA,CACA,oCACA,WACA,wBACA,CACA,eACA,oBACA,CACA,CACA,oCACA,mBACA,qBACA,CAEA,CACA,YAEA,0BAAA,CACA,sBAAA,CAFA,mBAGA,CACA,WAGA,iCAAA,CAKA,kBAAA,CAJA,uBAAA,CAOA,+BAAA,CANA,cAAA,CAOA,eAAA,CAFA,qBAAA,CAJA,gBAAA,CAGA,eAAA,CAFA,YAAA,CALA,WAAA,CAWA,UACA,CACA,6BACA,uBACA,CAFA,wBACA,uBACA,CACA,yCACA,gBACA,CACA,yDAIA,aAAA,CAEA,cAAA,CALA,cAAA,CACA,eAAA,CACA,gBAAA,CAEA,iBAEA,CACA,wCACA,+BAAA,CACA,qBACA,CACA,qBACA,qBACA,CACA,kDACA,qBACA,CACA,wDACA,aAAA,CAEA,WAAA,CACA,0BAAA,CAFA,UAGA,CACA,kDACA,qBACA,CACA,qBACA,qBACA,CACA,mCACA,qBACA,CAIA,iHACA,iCACA,CAOA,0JACA,oCACA,CACA,8CACA,iCACA,CACA,0DACA,+BAAA,CACA,yBACA,CACA,4BACA,uBACA,CACA,8BACA,sBACA,CACA,sEACA,uBAAA,CACA,+BAAA,CACA,yBACA,CACA,4BAMA,gbAAA,CAEA,uBAAA,CADA,2BAAA,CANA,UAAA,CACA,oBAAA,CAGA,WAAA,CAFA,eAAA,CACA,UAKA,CACA,0BACA,aAAA,CACA,+BAAA,CACA,yBACA,CAEA,gCAEA,yBAAA,CADA,sBAEA,CAEA,mBACA,uBAAA,CACA,qBAEA,CACA,mBACA,wBAAA,CACA,kBAAA,CACA,cAAA,CACA,eACA,CACA,kBACA,kBACA,CACA,6BACA,UACA,CAOA,gIACA,kBACA,CACA,aAEA,+BAAA,CACA,eACA,CACA,uCAJA,aAMA,CACA,qDACA,oBACA,CACA,sCACA,uBAAA,CACA,+BAAA,CACA,eACA,CAJA,iCACA,uBAAA,CACA,+BAAA,CACA,eACA,CACA,uBACA,eACA,CACA,4CACA,sBACA,CACA,yDAaA,sBAAA,CAFA,4BAAA,CAHA,kBAAA,CAGA,uBAAA,CACA,gCAAA,CAVA,UAAA,CAKA,WAAA,CAHA,QAAA,CACA,oBAAA,CAJA,iBAAA,CAEA,OAAA,CAGA,UAQA,CACA,sBACA,kBACA,CAEA,YAUA,uBAAA,CADA,qBAAA,CANA,cAAA,CACA,eAAA,CACA,gBAAA,CAEA,cAAA,CADA,aAKA,CACA,wBAXA,oBAAA,CAOA,iBAAA,CANA,kBAyBA,CAfA,YAcA,kBAAA,CAFA,iBAAA,CALA,UAAA,CAHA,cAAA,CAMA,4BAAA,CADA,WAAA,CAIA,SAAA,CAZA,wBAAA,CACA,qBAAA,CACA,oBAAA,CAKA,UAOA,CAwBA,SACA,iBACA,CAKA,mCACA,4BAAA,CACA,UACA,CAEA,WACA,QAAA,CACA,SACA,CACA,4EAEA,uBAAA,CACA,QACA,CACA,oBACA,qBACA","sourcesContent":["<template>\n    <div>\n        <ValidationObserver v-slot=\"{ invalid }\">\n            <div class=\"row mb-5 mx-auto\">\n                <div class=\"col-12 col-md-6\">\n                    <label class=\"travel-label\">Куда</label>\n                    <ValidationProvider name=\"country\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                        <multiselect\n                            v-model=\"new_tour.TourInfo.country\"\n                            :options=\"countries\"\n                            :option-height=\"25\"\n                            placeholder=\"Выберите страну\"\n                            :show-labels=\"false\"\n                            :maxHeight=\"200\"\n                            label=\"Name\"\n                            track-by=\"Name\"\n                            :loading=\"isCountriesLoading\"\n                            :disabled=\"isCountriesLoading\"\n                            :allow-empty=\"false\"\n                            @input=\"chooseNewResortCountry\"\n                        >\n                            <template slot=\"singleLabel\" slot-scope=\"props\">\n                                <b :class=\"'tc-flag-'+props.option.Id\"></b>\n                                <span class=\"option__desc\">\n                                <span class=\"option__title\">{{ props.option.Name }}</span>\n                            </span>\n                            </template>\n                            <template slot=\"option\" slot-scope=\"props\">\n                                <b :class=\"'tc-flag-'+props.option.Id\"></b>\n                                <span class=\"option__desc\">\n                                <span class=\"option__title\">{{ props.option.Name }}</span>\n                            </span>\n                            </template>\n                        </multiselect>\n                        <span class=\"validate-error\">{{ errors[0] }}</span>\n                    </ValidationProvider>\n                </div>\n                <div class=\"col-12 col-md-6\">\n                    <label class=\"travel-label\">Курорт</label>\n                    <ValidationProvider name=\"resort\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                        <multiselect\n                            v-model=\"new_tour.TourInfo.resort\"\n                            :options=\"cities\"\n                            placeholder=\"Введите название курорта\"\n                            :show-labels=\"false\"\n                            :maxHeight=\"200\"\n                            label=\"Name\"\n                            track-by=\"Name\"\n                            :close-on-select=\"true\" :clear-on-select=\"true\"\n                            :loading=\"isCountriesLoading || isCitiesLoading\"\n                            :disabled=\"isCountriesLoading || isCitiesLoading\"\n                            @input=\"chooseNewResorts\"\n                            :preselectFirst=\"true\"\n                        >\n                            <template slot=\"selection\" slot-scope=\"{ values, search, isOpen }\">\n                            <span\n                                class=\"multiselect__single\"\n                                v-if=\"values.length>3 && !isOpen\"\n                            >\n                                {{ values.length }} {{values.length | pluralizeResorts }} {{values.length | pluralizeChoose}}\n                            </span>\n                            </template>\n                        </multiselect>\n                        <span class=\"validate-error\">{{ errors[0] }}</span>\n                    </ValidationProvider>\n                </div>\n                <div class=\"col-12 col-md-6\">\n                    <label class=\"travel-label\">Отель</label>\n                    <ValidationProvider name=\"hotel\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                        <multiselect\n                            v-model=\"new_tour.TourInfo.hotel\"\n                            :options=\"hotels\"\n                            placeholder=\"Введите название отеля\"\n                            :show-labels=\"false\"\n                            :maxHeight=\"200\"\n                            label=\"Name\"\n                            track-by=\"Name\"\n                            :close-on-select=\"true\" :clear-on-select=\"true\"\n                            :loading=\"isCountriesLoading || isHotelsLoading\"\n                            :disabled=\"isCountriesLoading || isHotelsLoading\"\n                            :allow-empty=\"false\"\n                            @input=\"chooseNewHotel\"\n                            :preselectFirst=\"true\"\n                        >\n\n                            <template slot=\"selection\" slot-scope=\"{ values, search, isOpen }\">\n                            <span\n                                class=\"multiselect__single\"\n                                v-if=\"values.length>3 && !isOpen\"\n                            >\n                                {{ values.length }} {{values.length | pluralizeHotels}} {{values.length | pluralizeChoose}}\n                            </span>\n                            </template>\n                            <template slot=\"singleLabel\" slot-scope=\"props\">\n                            <span class=\"option__desc\">\n                                <span class=\"option__title\">{{ props.option.Name }}</span>\n                                <span class=\"option__title\" style=\"float:right\" v-if=\"props.option.CommonRate!=0\">{{ props.option.CommonRate }}</span>\n                                <span class=\"values__hotel-rating\" style=\"float:right\" v-if=\"props.option.StarName\">{{ props.option.StarName }}</span>\n                            </span>\n                            </template>\n                            <template slot=\"option\" slot-scope=\"props\">\n                                <div class=\"option__desc\">\n                                    <span class=\"option__title\">{{ props.option.Name }}</span>\n                                    <span class=\"option__title\" style=\"float:right\" v-if=\"props.option.CommonRate!=0\">{{ props.option.CommonRate }}</span>\n                                    <span class=\"values__hotel-rating\" style=\"float:right\" v-if=\"props.option.StarName\">{{ props.option.StarName }}</span>\n                                </div>\n                            </template>\n                        </multiselect>\n                        <span class=\"validate-error\">{{ errors[0] }}</span>\n                    </ValidationProvider>\n                </div>\n                <div class=\"col-12 col-md-6\">\n                    <label class=\"travel-label\">Питание</label>\n                    <ValidationProvider name=\"meal\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                        <multiselect\n                            v-model=\"new_tour.TourInfo.meal\"\n                            :options=\"meals\"\n                            :option-height=\"25\"\n                            placeholder=\"Выберите питание\"\n                            :show-labels=\"false\"\n                            :maxHeight=\"200\"\n                            label=\"Name\"\n                            track-by=\"Name\"\n                            :close-on-select=\"true\" :clear-on-select=\"true\"\n                            @input=\"chooseNewMeal\"\n                            :allow-empty=\"false\"\n                        >\n                            <template slot=\"singleLabel\" slot-scope=\"props\">\n                            <span class=\"option__desc\">\n                                <span class=\"option__title\">{{props.option.Name}}</span>\n                            </span>\n                            </template>\n                            <template slot=\"option\" slot-scope=\"props\">\n                                <div>\n                                    <div class=\"option__desc\">\n                                        <span class=\"option__title\">{{props.option.Name}}</span>\n                                    </div>\n                                    <div class=\"row align-items-center m-auto\" style=\"width: 100%; height: 100%\">\n                                        <p class=\"\" style=\"font-size: 12px; margin:0px;\">{{props.option.desc}}</p>\n                                    </div>\n                                </div>\n                            </template>\n                        </multiselect>\n                        <span class=\"validate-error\">{{ errors[0] }}</span>\n                    </ValidationProvider>\n                </div>\n                <div class=\"col-12 col-md-6\">\n                    <label class=\"travel-label\">Категория номера</label>\n                    <ValidationProvider name=\"room_type\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                        <div class=\"row multiselect__tags align-items-center justify-content-center m-auto\" style=\"width: 100%;\">\n                            <input type=\"text\" class=\" multiselect__input price\" v-model=\"new_tour.TourInfo.room_type\" placeholder=\"Категория номера\">\n                        </div>\n                        <span class=\"validate-error\">{{ errors[0] }}</span>\n                    </ValidationProvider>\n                </div>\n                <div class=\"col-12 col-md-6\">\n                    <div class=\"row align-items-center m-auto\" style=\"width: 100%;\">\n                        <label class=\"travel-label\">Даты тура</label>\n                        <ValidationProvider name=\"date_range\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                            <div class=\"row align-items-center justify-content-center m-auto w-100\" style=\"width: 100%;\">\n                                <VueHotelDatepicker\n                                    :startDate=\"new_tour.TourInfo.date_range.start\"\n                                    :endDate=\"new_tour.TourInfo.date_range.end\"\n                                    :placeholder=\"'Даты тура'\"\n                                    :format=\"'DD/MM/YYYY'\"\n                                    :weekList=\"['Вс.', 'Пн.', 'Вт.', 'Ср.', 'Чт.', 'Пт.', 'Сб.']\"\n                                    :monthList=\"['Янв.', 'Фев.', 'Мар.', 'Апр.', 'Май.', 'Июн.', 'Июл.', 'Авг.', 'Сен.', 'Окт', 'Ноя.', 'Дек.']\"\n                                    :fromText=\"'С'\"\n                                    :toText=\"'По'\"\n                                    :resetText=\"'Очистить'\"\n                                    :confirmText=\"'Подтвердить'\"\n                                    :mobile=\"'mobile'\"\n                                    class=\"w-100\"\n                                    @update=\"changeNewDateRange\"\n                                />\n                            </div>\n                            <span class=\"validate-error\">{{ errors[0] }}</span>\n                        </ValidationProvider>\n                    </div>\n                </div>\n                <div class=\"col-md-6 col-12\">\n                    <div class=\"row align-items-center m-auto\" style=\"width: 100%;\">\n                        <label class=\"travel-label\">Цена</label>\n                        <ValidationProvider name=\"price\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                            <b-form-input :type=\"'number'\" number class=\"travel-input\" v-model=\"new_tour.TourInfo.price\" step=\"1\" min=\"0\" @input=\"changeNewPrice\">\n                            </b-form-input>\n                            <span class=\"validate-error\">{{ errors[0] }}</span>\n                        </ValidationProvider>\n                    </div>\n                </div>\n                <div class=\"col-md-6 col-12\">\n                    <div class=\"row align-items-center m-auto\" style=\"width: 100%;\">\n                        <label class=\"travel-label\">Выплачено</label>\n                        <ValidationProvider name=\"left\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center;\">\n                            <b-form-input :type=\"'number'\" number class=\"travel-input price\" v-model=\"new_tour.TourInfo.paid\" step=\"1\" min=\"0\" @input=\"changeNewPrice\">\n                            </b-form-input>\n                            <span class=\"validate-error\">{{ errors[0] }}</span>\n                        </ValidationProvider>\n                    </div>\n                </div>\n                <div class=\"col-md-12\">\n                    <div class=\"row align-items-center justify-content-center mx-auto mt-3\" style=\"width: 100%;\">\n                        <label class=\"travel-label\">Дополнительные услуги</label>\n                        <b-form-textarea rows=\"4\" cols=\"80\"\n                                  class=\"travel-input\"\n                                  placeholder=\"Дополнительные услуги\"\n                                  v-model=\"new_tour.TourInfo.services\"\n                                  style=\"padding: 5px 15px; border: 1px solid #f08b23; border-radius: 20px !important;\"\n                        >\n                    </b-form-textarea>\n                    </div>\n                </div>\n                <div class=\"col-md-12\">\n                    <label class=\"travel-label\">Авиабилеты</label>\n                    <avia-tickets-list :avia_tickets=\"new_tour.TourInfo.avia_tickets\"></avia-tickets-list>\n                </div>\n                <div class=\"col-md-12\">\n                    <label class=\"travel-label\">Пассажиры</label>\n                    <passengers-list :passengers=\"new_tour.TourInfo.passengers\"></passengers-list>\n                </div>\n                <div class=\"col-md-12\">\n                    <label class=\"travel-label\">Файлы для клиента</label>\n                    <div class=\"form-group row mt-2\">\n                        <div class=\"col-md-12\">\n                            <div class=\"large-12 medium-12 small-12 filezone\">\n                                <input type=\"file\" id=\"new_files\" ref=\"new_tour_files\" multiple v-on:change=\"handleNewFiles()\"/>\n                                <p>\n                                    Перетащите файл(ы) сюда<br>или нажмите для поиска\n                                </p>\n                            </div>\n                            <label class=\"travel-label\">Или запишите голосовое сообщение</label>\n                            <div class=\"d-flex justify-content-center mt-2\">\n                                <vue-record-audio :mode=\"'press'\" @stream=\"onStream\" @result=\"onResult\"/>\n                            </div>\n                            <div\n                                class=\"file-listing\"\n                            >\n                                <div class=\"list-group-item\" v-for=\"(file, index) in new_tour.TourInfo.files\" :key=\"file.name\">\n                                    <div class=\"row m-auto w-100 align-items-center justify-content-center\">\n                                        <div class=\"col-4 text-center\">\n                                            <img class=\"preview\" v-bind:ref=\"'preview'+parseInt(index)\"/>\n                                        </div>\n                                        <div class=\"col-8\">\n                                            <div v-if=\"file.type=='file'\">\n                                                <p class=\"travel-label\">{{file.data.name}}</p>\n                                            </div>\n                                            <div v-if=\"file.type=='audio'\">\n                                                <audio-player :audio=\"file\"></audio-player>\n                                            </div>\n                                        </div>\n                                    </div>\n                                    <div class=\"remove-container\">\n                                        <a class=\"remove\" v-on:click=\"removeFile(new_tour.TourInfo.files, index)\" v-if=\"loading == false\">Отменить</a>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n            <div class=\"row w-100 m-auto\">\n                <div class=\"col-6 col-md-6 contact-btn text-center\">\n                    <button class=\"btn btn-travel\" style=\"width: 100%\" @click=\"back\">\n                        Отмена\n                    </button>\n                </div>\n                <div class=\"col-6 col-md-6 contact-btn text-center\">\n                    <button type=\"submit\" class=\"btn btn-travel\" style=\"width: 100%\" @click=\"saveClientTour\" :disabled=\"invalid||loading\">\n                        <div class=\"row align-items-center justify-content-center m-auto w-100 h-100 text-center\">\n                            <span v-if=\"loading\" class=\"spinner-border spinner-border-sm mx-2\" role=\"status\" aria-hidden=\"true\"></span>\n                            Сохранить\n                        </div>\n                    </button>\n                </div>\n            </div>\n        </ValidationObserver>\n    </div>\n\n</template>\n\n<script>\n    import {mask} from 'vue-the-mask'\n    import moment from 'moment'\n    export default {\n        name: \"Create\",\n        data() {\n            return {\n                city_options: [\n                    'Донецк',\n                    'Ростов-на-Дону'\n                ],\n                new_tour:{\n                    UserId:'',\n                    TourId:'',\n                    TourInfo: {\n                        country:{Id:150,Name:\"Россия\",Alias:\"Russia\",Flags:8,HasTickets:true,HotelIsNotInStop:true,IsProVisa:false,IsVisa:false,OriginalName:\"Russia\",Rank:0,TicketsIncluded:true},\n                        resort:{Name:'Любой'},\n                        hotel: {Name:'Любой'},\n                        room_type:'',\n                        meal:'',\n                        tour_operator:'',\n                        date_range:{start:'', end:''},\n                        price:'',\n                        paid:'',\n                        left:'',\n                        services:'',\n                        avia_tickets:[],\n                        passengers:[],\n                        files:[],\n                    },\n                    StartAt:'',\n                    EndAt:'',\n                    Comment:''\n                },\n                new_mode:false,\n                meals: [\n                    { Id:'115', Name:'AI', desc:'завтраки, обеды, ужины, напитки'},\n                    { Id:'114',Name:'BB', desc:'завтраки'},\n                    { Id:'112',Name:'FB', desc:'завтраки, обеды, ужины'},\n                    { Id:'121',Name:'FB+', desc:'завтраки, обеды, ужины - расширенное меню'},\n                    { Id:'113',Name:'HB', desc:'завтраки, ужины'},\n                    { Id:'122',Name:'HB+', desc:'завтраки, ужины - расширенное меню'},\n                    { Id:'117',Name:'RO', desc:'без питания'},\n                    { Id:'116',Name:'UAI', desc:'завтраки, обеды, ужины, напитки - расширенное меню'},\n                    { Id:'129',Name:'SC', desc:'самообслуживание'},\n                ],\n                loading: false,\n                new_files:[],\n                start: undefined,\n                end: undefined,\n                flag:false,\n                seconds:0,\n                interval:'',\n            }\n        },\n        created() {\n            if (this.countries.length===0) {\n                this.$store.dispatch('getCountries', {town_id:0});\n            }\n            this.$store.dispatch('getCities', this.new_tour.TourInfo.country.Id);\n            this.$store.dispatch('getHotels', {country_id:this.new_tour.TourInfo.country.Id, town_id:'0', star_id:'0'});\n        },\n        computed: {\n            menu() {\n                return this.$store.getters.menu\n            },\n            isAdmin : function(){ return this.$store.getters.isAdmin},\n            isManager : function(){ return this.$store.getters.isManager},\n            tourModule() {\n                return this.$store.getters.tourModule\n            },\n            countries() {\n                return this.$store.getters.countries;\n            },\n            cities() {\n                return this.$store.getters.cities;\n            },\n            hotels() {\n                return this.$store.getters.hotels;\n            },\n            branches(){\n                return this.$store.getters.branches\n            },\n\n            //loadings\n            isCountriesLoading() {\n                return this.$store.getters.isCountriesLoading;\n            },\n            isCitiesLoading() {\n                return this.$store.getters.isCitiesLoading;\n            },\n            isHotelsLoading() {\n                return this.$store.getters.isHotelsLoading;\n            },\n        },\n        methods: {\n            back() {\n                if(this.isAdmin){\n                    this.$router.push('/admin/client-tours/'+this.$route.params.id)\n                }\n                if(this.isManager){\n                    this.$router.push('/manager/client-tours/'+this.$route.params.id)\n                }\n            },\n            chooseNewResortCountry(value){\n                this.new_tour.TourInfo.resort={Name:'Любой'};\n                this.new_tour.TourInfo.hotel={Name:'Любой'};\n                this.new_tour.TourInfo.country = value;\n                this.$store.dispatch('getCities', this.new_tour.TourInfo.country.Id);\n                this.$store.dispatch('getHotels', {country_id:this.new_tour.TourInfo.country.Id, town_id:'0', star_id:'0'});\n            },\n            chooseNewResorts(value){\n                this.new_tour.TourInfo.resort = value;\n                if(this.new_tour.TourInfo.resort.Name != 'Любой')\n                {\n                    // this.new_tour.TourInfo.resort = this.tourModule.resorts[0];\n                    var town_id = this.new_tour.TourInfo.resort.Id;\n                    //поиск отелей по городам-курортам\n                    this.$store.dispatch('getHotels', {country_id:this.new_tour.TourInfo.country.Id, town_id:town_id, star_id:'0'}).then(() => {\n                        if(this.hotels.findIndex(item=> item.Id == this.new_tour.TourInfo.hotel.Id) == -1)\n                        {\n                            this.new_tour.TourInfo.hotel = {Name:'Любой'};\n                        }\n                    });\n                }\n                else {\n                    //если городов-курортов не выбрано, то поиск отелей по стране\n                    this.$store.dispatch('getHotels', {country_id:this.new_tour.TourInfo.country.Id, town_id:'0', star_id:'0'});\n                }\n            },\n            chooseNewHotel(value) {\n                this.new_tour.TourInfo.hotel = value;\n            },\n            changeNewDateRange(date_object) {\n                this.new_tour.TourInfo.date_range=date_object;\n            },\n            chooseNewMeal(value){\n                this.new_tour.TourInfo.meal = value;\n            },\n            changeNewPrice(){\n                this.new_tour.TourInfo.left = this.new_tour.TourInfo.price - this.new_tour.TourInfo.paid;\n            },\n            handleNewFiles() {\n                let uploadedFiles = this.$refs.new_tour_files.files;\n                for(var i = 0; i < uploadedFiles.length; i++) {\n                    if (/\\.(jpe?g|png|svg|pdf|txt|docx|doc)$/i.test(uploadedFiles[i].name))\n                    {\n                        var date = new Date();\n                        this.new_tour.TourInfo.files.push({type:'file', name:uploadedFiles[i].name, data:uploadedFiles[i], date:date});\n                        this.getImagePreviews(this.new_tour.TourInfo.files);\n                    }\n                    else\n                    {\n                        break;\n                    }\n                }\n            },\n            onResult(data) {\n                var time = Date.now();\n                var date = new Date();\n                this.new_tour.TourInfo.files.push({\n                    type: 'audio',\n                    name: 'record-'+time+'.mp3',\n                    src: window.URL.createObjectURL(data),\n                    data: data,\n                    date: date,\n                    seconds: this.seconds\n                })\n                this.countSeconds();\n                this.getImagePreviews(this.new_tour.TourInfo.files);\n            },\n            saveClientTour(){\n                this.loading = true;\n                this.new_tour.UserId = this.$route.params.id;\n                this.new_tour.StartAt = moment(this.new_tour.TourInfo.date_range.start, \"DD-MM-YYYY\");\n                this.new_tour.EndAt = moment(this.new_tour.TourInfo.date_range.end, \"DD-MM-YYYY\");\n\n                let formData = new FormData();\n                formData.append(\"UserTour\", JSON.stringify(this.new_tour));\n\n                for (var i = 0; i < this.new_tour.TourInfo.files.length; i++) {\n                    let file = this.new_tour.TourInfo.files[i].data;\n\n                    formData.append('files[' + i + ']', file);\n                }\n\n                this.$store.dispatch('newClientTour', formData)\n                    .then(response => {\n                        this.sendMessage('Тур клиента успешно добавлен');\n\n                        this.new_tour= {\n                            UserId:'',\n                            TourId:'',\n                            TourInfo: {\n                                country:{Id:150,Name:\"Россия\",Alias:\"Russia\",Flags:8,HasTickets:true,HotelIsNotInStop:true,IsProVisa:false,IsVisa:false,OriginalName:\"Russia\",Rank:0,TicketsIncluded:true},\n                                resort:{Name:'Любой'},\n                                hotel: {Name:'Любой'},\n                                meal:'',\n                                tour_operator:'',\n                                date_range:{start:'', end:''},\n                                price:'',\n                                paid:'',\n                                left:'',\n                                services:'',\n                                avia_tickets:[],\n                                passengers:[],\n                                files:[],\n                            },\n                            StartAt:'',\n                            EndAt:'',\n                            Comment:'',\n                            Archive:false\n                        }\n                        this.$store.dispatch('getCities', this.new_tour.TourInfo.country.Id);\n                        this.$store.dispatch('getHotels', {country_id:this.new_tour.TourInfo.country.Id, town_id:'0', star_id:'0'});\n                        this.loading = false;\n                    });\n\n            },\n\n            //both\n            getImagePreviews(arr) {\n                for ( let i = 0; i < arr.length; i++ ) {\n                    if ( /\\.(jpe?g|png|svg)$/i.test(arr[i].name)) {\n                        let reader = new FileReader();\n                        reader.addEventListener(\"load\", function() {\n                            this.$refs['preview'+parseInt(i)][0].src = reader.result;\n                        }.bind(this), false);\n                        reader.readAsDataURL(arr[i].data);\n                    }\n                    else {\n                        this.$nextTick(function() {\n                            this.$refs['preview'+parseInt(i)][0].src = '/images/LOGO-1.png';\n                        });\n                    }\n                }\n            },\n            removeFile( arr, key ) {\n                arr.splice( key, 1 );\n                this.getImagePreviews(arr);\n            },\n            onStream(stream) {\n                this.countSeconds();\n                console.log('Got a stream object:', stream);\n            },\n            countSeconds() {\n                this.flag = !this.flag;\n                if (this.flag == true) {\n                    this.seconds=0;\n                    this.interval = setInterval(this.incrementSeconds, 1000);\n                }\n                else {\n                    clearInterval(this.interval)\n                }\n            },\n            incrementSeconds() {\n                this.seconds += 1;\n            },\n\n            sendMessage(message) {\n                this.$notify({\n                    group: 'info',\n                    type: 'travel',\n                    title: 'Сообщение от ВПУТЬ',\n                    text: message\n                });\n            },\n        },\n        filters: {\n            pluralizeResorts: function(n) {\n                if(n === 1)\n                {\n                    return 'курорт'\n                }\n                if (n>1 && n<5) {\n                    return 'курорта'\n                }\n                if (n>4 && n<20) {\n                    return 'курортов'\n                }\n                if (n>19) {\n                    var last = n%10;\n                    if(last === 1)\n                    {\n                        return 'курорт'\n                    }\n                    if(last>1 && last<5)\n                    {\n                        return 'курорта'\n                    }\n                    if (last>4 && last<10 && last === 0 ) {\n                        return 'курортов'\n                    }\n                    return 'курортов'\n                }\n                return 'курортов'\n            },\n            pluralizeHotels: function(n) {\n                if(n === 1)\n                {\n                    return 'отель'\n                }\n                if (n>1 && n<5) {\n                    return 'отеля'\n                }\n                if (n>4 && n<20) {\n                    return 'отелей'\n                }\n                if (n>19) {\n                    var last = n%10;\n                    if(last === 1)\n                    {\n                        return 'отель'\n                    }\n                    if(last>1 && last<5)\n                    {\n                        return 'отеля'\n                    }\n                    if (last>4 && last<10 && last === 0 ) {\n                        return 'отелей'\n                    }\n                    return 'отелей'\n                }\n                return 'отелей'\n            },\n            pluralizeChoose: function(n) {\n                if(n === 1)\n                {\n                    return 'выбран'\n                }\n                return 'выбрано'\n            }\n        },\n        directives: {mask}\n    }\n</script>\n\n<style>\n    .btn-travel {\n        color: #fff !important;\n        background: linear-gradient(0deg, #ffbf00 0%, #f08b23 100%);\n        background-color: rgba(0, 0, 0, 0);\n        background-color: #f08b23 !important;\n        border-color: #f08b23 !important;\n        box-shadow: 0 2px 2px 0 rgba(156, 39, 176, 0.14), 0 3px 1px -2px rgba(156, 39, 176, 0.2), 0 1px 5px 0 rgba(156, 39, 176, 0.12) !important;\n    }\n    @media only screen and (max-width:767.98px) {\n        .vdp-container:not(.desktop) .vdp-picker {\n            width: 100% !important;\n            padding: 8px\n        }\n        .tour-module .cont .row{\n            width: 100% !important;\n        }\n        .qty input {\n            border: 0;\n            width: 50%;\n        }\n    }\n    @media screen and (max-width: 477px) {\n        .vdp-input {\n            min-width: 100% !important;\n        }\n        .vdp-container {\n            width: 100% !important;\n        }\n    }\n    @media screen and (max-width: 576px) {\n        .tour-module .cont {\n            height: 100% !important;\n        }\n\n    }\n    .vdp-datepicker__calendar {\n        position: absolute;\n        z-index: 2!important;\n        padding: 18px!important;\n        background: #fff;\n        width: 300px;\n        border: 1px solid #ccc;\n    }\n    .vdp-datepicker input {\n        /*min-width: 300px;*/\n        padding: 8px;\n        border: 1px solid #ffa500 !important;\n        color: #062348!important;\n        font-size: 16px;\n        line-height: 32px;\n        outline: none;\n        border-radius: 20px;\n        min-height: 40px;\n        height: 40px !important;\n        font-family: \"Open Sans\" !important;\n        font-weight: 900;\n        width: 100%;\n    }\n    .vdp-datepicker input::placeholder {\n        color: #062348 !important;\n    }\n    .vdp-datepicker__calendar .day-header\n    {\n        color: #062348;\n        font-family: \"Open Sans\" !important;\n 